export default {
	data() {
		return {
			loadStatus: "more",
			pageNum: 1,
			pageSize: 10,
			total: "",
			list: [],
			myEmployeeId:"",
			searchVal: "",
			statusArr: [
				"全部",
				"未付款",
				"已付款"
			],
			status: "按状态筛选"
		}
	},
	onLoad() {
		this.clearList();
		this.getList(1);
	},
	methods: {
		reload(){
			this.clearList();
			this.getList(1);	
		},
		statusPicker: function(e) {
			const that = this;
			that.status = that.statusArr[e.detail.value];
		},
		
		gotoDetail(id){
			const that = this;
			that.$Router.push({
				path:"/pages/my/manageNavs/notice/detail",
				query:{
					id:id
				}
			})
		},
		gotoAdd(){
			const that =this;
			that.$Router.push({
				path:"/pages/my/manageNavs/notice/add"
			})
		},
		getList(startPage) {
			const that = this;
			let param = {
				dynamicParam: that.searchVal,
				queryStatus:that.queryStatus,
				startPage: startPage,
				pageSize: that.pageSize
			}
			that.loadStatus = "loading";
			that.$api.noticeListB(param).then(res => {
				if (res.success) {
					let result = res.datas.rows;
		
					for (var i in result) {
						result[i].createEmployeeHeadImg = result[i].createEmployeeHeadImg?result[i].createEmployeeHeadImg:"../../../../static/images/defaultUserPic.png";
						that.list.push(result[i]);
					}
					let total = res.datas.total;
					that.total = total;
					that.myEmployeeId = res.datas.myEmployeeId;
					let totalPageNum = Math.ceil(total / that.pageSize);
					if (parseInt(totalPageNum) > parseInt(that.pageNum)) {
						that.pageNum++;
						that.loadStatus = "more";
					} else {
						that.loadStatus = "noMore";
					}
				} else {
					uni.showToast({
						title: res.message,
						icon: "none"
					})
				}
			})
		},
		delNotice(id,index){
			const that = this;
			uni.showModal({
				content: "确定要删除公告吗？",
				confirmText: "确认删除",
				cancelText: "取消",
				success: function(res) {
					if (res.confirm) {
						var param={
							id:id
						}
						that.$api.noticeDelete(param).then(res => {
							if (res.success) {
								that.list.splice(index, 1);
								uni.showToast({
									title: "删除成功"
								})
							} else {
								uni.showToast({
									title: res.message,
									icon: "none"
								})
							}
						})
						
					} else if (res.cancel) {
			
					}
				}
			});
		},
		editNotice(id){
			const that =this;
			that.$Router.push({
				path:"/pages/my/manageNavs/notice/edit",
				query:{
					id:id
				}
			})
		},
		clearList() {
			const that = this;
			that.pageNum = 1;
			that.total = "";
			that.list = [];
		},
		search() {
			const that = this;
			that.clearList();
			that.getList(1);
		}
	}
}
